@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.slider_container{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
}

.slider{
  width: 300vw;
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 0;
  transition: left .6s;
  background-color: #000;
}


.page1_container {
  width: 100vw;
  height: 100vh;
  transition: background 2s;
  position: relative;
  overflow: hidden;
}

.mobile_navigation {
  display: none;
}

.hill {
  position: absolute;
  bottom: 0;
  left: 0px;
  width: 100%;
  z-index: 100;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
}

.color_white {
  color: #f9f9f9;
}

.hero {
  display: flex;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -80%);
  font-family: 'Sansita Swashed', cursive;
  font-size: 150px;
  z-index: 50;
  bottom: 30%;
}

.st {
  -webkit-animation: appear 1s forwards;
          animation: appear 1s forwards;
}

.nd {
  opacity: 0;
  transform: translateY(200%);
  -webkit-animation: appear 0.7s forwards 0.5s;
          animation: appear 0.7s forwards 0.5s;
}

.moon {
  position: absolute;
  top: 10%;
  left: 50%;
  z-index: 110;
  object-fit: contain;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-animation: moonGlow 10s infinite;
          animation: moonGlow 10s infinite;
  cursor: pointer;
  background: url('https://www.pngkey.com/png/full/168-1689635_waning-moon-moon.png')
    center center/cover no-repeat;
  transform: translateX(-50%) translateY(-50vh);
  transition: transform 2s;
}

.sun {
  position: absolute;
  top: 10%;
  left: 50%;
  z-index: 110;
  width: 400px;
  height: 400px;
  -webkit-animation: sunGlow 10s infinite;
          animation: sunGlow 10s infinite;
  cursor: pointer;
  background: url('https://gallery.yopriceville.com/var/resizes/Free-Clipart-Pictures/Decorative-Elements-PNG/Sun_Effect_Transparent_PNG_Clip_Art_Image.png?m=1507172114')
    center center/cover no-repeat;
  transform: translateX(-50%) translateY(-50vh);
  transition: transform 2s;
}

@-webkit-keyframes fall {
  from {
    transform: translateX(-50%) translateY(-100%);
  }
  to {
    transform: translateX(-50%) translateY(0%);
  }
}

@keyframes fall {
  from {
    transform: translateX(-50%) translateY(-100%);
  }
  to {
    transform: translateX(-50%) translateY(0%);
  }
}

@-webkit-keyframes moonGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@keyframes moonGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@-webkit-keyframes sunGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@keyframes sunGlow {
  0% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }

  50% {
    -webkit-filter: brightness(150%);
            filter: brightness(150%);
  }

  100% {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
    transform: translateY(200%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateY(200%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1150px) {
  .hero {
    display: block;
    font-size: 100px;
    bottom: 15%;
    text-align: center;
  }

  .hill {
    position: absolute;
    bottom: 0;
    left: 0px;
    width: 100%;
    height: 300px;
    z-index: 100;
    object-fit: cover;
  }

  .sun {
    width: 180px;
    height: 180px;
  }

  .moon {
    width: 130px;
    height: 130px;
  }

  .btn {
    display: none !important;
  }

  .mobile_navigation {
    display: flex;
    position: absolute;
    bottom: 25%;
    left: 50%;
    z-index: 150;
    transform: translateX(-50%);
  }

  .about_me_mobile,
  .my_work_mobile {
    border: none;
    background-color: #fff;
    padding: 5px;
    border-radius: 10px;
    align-items: center;
    justify-content: center;
    width: 130px;
    font-size: 23px;
    font-family: 'Roboto', sans-serif;
    outline: none;
  }
  .about_me_mobile {
    margin-right: 10px;
    display: flex;
    flex-direction: row;
  }
  .my_work_mobile {
    margin-left: 10px;
    display: flex;
    flex-direction: row;
  }
}

@media only screen and (max-height: 750px) {
  .moon,
  .sun {
    width: 180px;
    height: 180px;
    top: 5%;
  }

  .moon {
    width: 130px;
    height: 130px;
  }

  .hero {
    bottom: 10%;
  }

  .mobile_navigation{
    bottom: 10%;
  }
}

@media (min-width: 1200px) {
  .hero {
    font-size: 100px;
    bottom: 35%;
  }
  .sun {
    width: 240px;
    height: 240px;
  }
}

@media (min-width: 1600px) {
  .hero {
    font-size: 150px;
    bottom: 30%;
  }
  .sun {
    width: 400px;
    height: 400px;
  }
}

.btn {
  border: none;
  align-self: center;
  display: block;
  font-size: 20px;
  background-color: #fff;
  width: 150px;
  padding: 10px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.about_me_btn {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  -webkit-animation: leftToRight 1s forwards;
          animation: leftToRight 1s forwards;
}
.my_work_btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-animation: rightToLeft 1s forwards;
          animation: rightToLeft 1s forwards;
}

.btn:hover {
  background-color: #333;
  color: #fff;
}

@-webkit-keyframes leftToRight {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(-50px);
  }
}

@keyframes leftToRight {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(-50px);
  }
}

@-webkit-keyframes rightToLeft {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(50px);
  }
}

@keyframes rightToLeft {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(50px);
  }
}

@media (max-width: 600px){
  .sun {
    width: 300px;
    height: 300px;
  }
}
.about_container {
  background-color: black;
  width: 100vw;
  height: 100%;
}

.work_container {
  background-color: black;
  height: 100%;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* Scrollbar */
html {
  --scrollbarBG: #333;
  --thumbBG: #90a4ae;
  background-color: #000;
}

body::-webkit-scrollbar {
  width: 11px;
}

body {
  scrollbar-width: thin;
  scrollbar-color: #90a4ae #333;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

body::-webkit-scrollbar-track {
  background: #333;
  background: var(--scrollbarBG);
}

body::-webkit-scrollbar-thumb {
  background-color: #90a4ae;
  background-color: var(--thumbBG);
  border-radius: 6px;
  border: 3px solid #333;
  border: 3px solid var(--scrollbarBG);
}

/*-------------*/
.text-black {
  color: #000;
}

html {
  background-color: #000;
}

body {
  font-family: 'Dosis', sans-serif;
  background-color: #f4f4f4;
  line-height: 1.5;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

img {
  width: 100%;
}

.btn-light,
.btn-dark {
  display: inline-block;
  padding: 0.7rem 2rem;
  border: none;
  cursor: pointer;
  transition: background 0.5s;
}

.btn-light {
  background-color: #f4f4f4;
  color: #333;
}

.btn-dark {
  background-color: #333;
  color: #fff;
}

button[class^='btn-']:hover,
a[class^='btn-']:hover,
input[class^='btn-']:hover {
  background-color: #531111;
  color: #fff;
}

#items {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  text-align: center;
}

#site {
  height: 100%;
}

.item {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15%;
  color: #f4f4f4;
  background: rgba(0, 0, 0, 0.5);
  transform: translateY(-150%);
  transition: opacity, transform 1s ease-out;
}

#launch {
  position: absolute;
  z-index: 3;
  bottom: 5%;
  transform: translateY(200%);
  transition: background 0.5s ease-in-out, color 0.5s ease-in-out,
    transform 1s ease-out;
}

#launch:hover {
  background: #333;
}

.item:hover .title {
  transform: none;
}

.item:hover #launch {
  transform: none;
}

.my {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#about {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 2rem;
  margin: 2rem;
  text-align: center;
  font-size: 1.2rem;
  color: #f4f4f4;
}
#about-img {
  width: 100%;
  height: 100%;
}
#about ul {
  text-align: left;
  font-weight: bold;
}
#about li {
  font-weight: normal;
}
#about-text {
  padding-top: 2rem;
  padding-left: 1rem;
  border-left: 5px solid #880909;
  background-color: rgba(0, 0, 0, 0.3);
  padding-bottom: 2rem;
}

.bold {
  font-weight: bolder;
}

html {
  position: relative;
}



@media (max-width: 500px) {


  body {
    line-height: 1;
  }

  #items {
    display: block;
    margin-top: 3rem;
  }
  .item {
    margin-top: 1rem;
  }
  .title {
    transform: none;
  }
  #launch {
    transform: none;
  }
  #about {
    display: block;
  }
  #about-img {
    margin-bottom: 1rem;
  }
  #about-text {
    padding: 0;
    border-left: none;
    border-top: 5px solid #690d0d;
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.7);
  }

  html {
    background: #000;
  }
}

svg{
  width: 800px;
  stroke-dasharray: 7413.63px;
  stroke-dashoffset: 7413.63px;
  -webkit-animation: animsvg 20s ease forwards;
          animation: animsvg 20s ease forwards;
}

@-webkit-keyframes animsvg{
  to{
    stroke-dashoffset: 0;
  }
}

@keyframes animsvg{
  to{
    stroke-dashoffset: 0;
  }
}


@media (max-width: 1150px){
  .back{
    display: flex !important;
  }  
}
